import { downloadImage, previewImage, chooseImage, uploadImage, getLocalImgData } from '@utils/ydk';

import html2canvas from 'html2canvas';

export default {
  data() {
    return {
      img: '',
      isShow: false,
    };
  },
  methods: {
    async onclick() {
      // const xxId = 'https://oimagea7.ydstatic.com/image?id=5922902393307803720&product=xue';
      // const { localId } = await downloadImage(xxId); // 下载普通资源文件没有问题  可以正常展示出来(localdata)
      this.isShow = true;

    },
    onClickHide() {
      this.isShow = false;
    }
  },
  mounted: async function () {

    const canvas = await html2canvas(document.getElementById('target'));
    const fullQuality = canvas.toDataURL("image/jpeg", 1.0);//转成base64
    this.img = fullQuality;

  }
};



